@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
   font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.titlebar-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid-container {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.titlebar {
	width: 100%;
	display: flex;
	flex-grow: 1;
	margin-top: 50px;
	align-items: center;
}

.titlebar-left {
	display: flex;
	width: 33.33%;
	font-weight: 400px;
	font-size: 20px;
}

.nav-item {
	margin-left: 15px;
	cursor: pointer;
}

.nav-item:hover {
	text-decoration: underline;
}

.titlebar-center {
	width: 33.33%;
	height: 100%;
}

.titlebar-right {
	width: 33.33%;
}
.title {
	height: 100%;
	width: 100%;
	font-size: 36px;
	margin-bottom: 0;
}

.byline {
	font-size: 16px;
	font-weight: 300px;
	margin-top: 0;
}

.grid {
	width: 100%;
	margin-top: 50px;
	display: inline;
}

.grid-item {
	width: 100%;
	display: inline;
}

.grid-img {
	width: 25%;
	margin-right: 10px;
	margin-left: 10px;
	margin-top: 10px;
}

.grid-item:hover {
	opacity: 0.5;
	cursor: pointer;
}
